<template>
  <div class="container">
    <div class="gauge-bg">
      <div class="gauge-middle"></div>
      <div class="gauge-overlay" :style="{transform: rotate}"></div>
      <div class="gauge-data">
        <span>{{ percentage }} %</span>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: ["percentage"],
  computed: {
    rotate() {
      const v = (this.percentage * 180) / 100;
      return `rotate(${v}deg)`;
    }
  }
};
</script>
<style>
.container {
  width: 320px;
  height: 160px;
  position: absolute;
  top: 0;
  overflow: hidden;
}

.gauge-bg {
  z-index: 1;
  width: 320px;
  height: 160px;
  position: absolute;
  background-color: #fff;
  border-radius: 160px 160px 0 0;
}

.gauge-middle {
  z-index: 3;
  position: absolute;
  background-color: rgb(64,64,64);
  width: 220px;
  height: 160px;
  top: 45px;
  margin-right: auto;
  margin-left: 50px;
  border-radius: 160px 160px 0 0;
}

.gauge-overlay {
  z-index: 2;
  position: absolute;
  background-color: #5df086;
  width: 320px;
  height: 160px;
  top: 160px;
  border-radius: 0 0 160px 160px;
  transform-origin: center top;
}

.gauge-data {
  z-index: 4;
  color: #5df086;
  position: absolute;
  width: 320px;
  bottom: 0;
  text-align: center;
  font-size: 24px;
}
</style>

