<template>
  <div>
    <div v-show="isLoading===true" class="lds-circle">
      <div></div>
      <h4>Loading</h4>
    </div>
    <div class="container">
      <div v-show="isLoading===false && isFinished===false" class="game">
        <div class="rules">
          <h2 class="panel">Stage {{playingVideoID+1}}</h2>
          <h2 class="panel-title">Happy Mood: {{happy | percentFormat}}</h2>
        </div>
        <div class="gauge">
          <gauge :percentage="Math.round(happy * 100)"></gauge>
        </div>
        <div class="video-container">
          <vue-plyr ref="player" :key="videoID[playingVideoID]">
            <div data-plyr-provider="youtube" :data-plyr-embed-id="videoID[playingVideoID]"></div>
          </vue-plyr>
        </div>
        <video ref="video" class="stream" playsinline autoplay></video>
        <canvas ref="canvas"></canvas>
      </div>

      <div v-show="(playingVideoID) > videoID.length">
        <div v-show="isFinished===true && isLaugh===false" class="resultW">
          <h1>Congratulations!!</h1>
          <h1>You completed all Stages</h1>
        </div>
        <div v-show="isFinished===true && isLaugh===true" class="result">
          <h1>Final Happy Mood: {{Score | percentFormat}}</h1>
          <h1>Smiled at Stage {{ smileStage + 1}} / {{ videoID.length }}</h1>
          <a ref="link" class="button" style="background-color: #9a4ef1"></a>
        </div>
      </div>
      <div v-if="nextStage===true">
        <modal>
          <h2 slot="header" style="color: rgb(64,64,64)">Congratulations!!</h2>
          <h3
            slot="body"
            style="color: rgb(64,64,64)"
          >You Pass Stage {{ playingVideoID+1 }} / {{ videoID.length }}!!!</h3>
          <a
            slot="footer"
            class="button"
            style="background-color: #9a4ef1"
            @click="toNextStage()"
          >Next</a>
        </modal>
      </div>
    </div>
  </div>
</template>

<script>
import * as faceapi from "face-api.js";
import modal from "./../components/modal";
import gauge from "./../components/gauge";
import { setInterval } from "timers";

export default {
  name: "stream",
  components: {
    modal,
    gauge
  },
  data() {
    return {
      facingMode: false,
      timeInterval: 0,
      isLoading: true,
      isFinished: false,
      isLaugh: false,
      nextStage: false,
      smileStage: 0,
      happy: 0,
      Score: 0,
      videoID: ["8d6voO-na6w", "IPXU-Q5fUiI", "Zt-exFj2U1c"],
      playingVideoID: 0
    };
  },
  mounted() {
    this.init();
  },
  filters: {
    percentFormat(value) {
      return Math.round(value * 100) + " %";
    }
  },
  methods: {
    async init() {
      await Promise.all([
        faceapi.nets.tinyFaceDetector.loadFromUri("https://jameshsu333.github.io/YouLaughYouLose/public/data/weights"),
        faceapi.nets.faceLandmark68Net.loadFromUri("https://jameshsu333.github.io/YouLaughYouLose/public/data/weights"),
        faceapi.nets.faceRecognitionNet.loadFromUri("https://jameshsu333.github.io/YouLaughYouLose/public/data/weights"),
        faceapi.nets.faceExpressionNet.loadFromUri("https://jameshsu333.github.io/YouLaughYouLose/public/data/weights")
      ]).then(this.startStream());
      this.onPlay();
    },
    startStream() {
      let video = this.$refs.video;
      navigator.mediaDevices
        .getUserMedia({
          audio: false,
          video: {
            width: 320,
            height: 180,
            frameRate: {
              min: 15,
              ideal: 30,
              max: 60
            },
            facingMode: this.facingMode ? "user" : "environment"
          }
        })
        .then(stream => {
          window.stream = stream;
          video.srcObject = stream;
        })
        .catch(err => {
          alert(err);
        });
    },
    async onPlay() {
      const video = this.$refs.video;
      const canvas = this.$refs.canvas;
      faceapi.matchDimensions(canvas, {
        width: 320,
        height: 180
      });
      setInterval(async () => {
        const faceDetectionTask = await faceapi
          .detectSingleFace(video, new faceapi.TinyFaceDetectorOptions())
          .withFaceLandmarks()
          .withFaceExpressions();

        this.happy = faceDetectionTask.expressions.happy;
        this.checkHappiness();
        this.checkPlayer();

        const results = faceapi.resizeResults(faceDetectionTask, {
          width: 320,
          height: 180
        });

        Promise.all([
          canvas.getContext("2d").clearRect(0, 0, canvas.width, canvas.height),
          faceapi.draw.drawDetections(canvas, results),
          faceapi.draw.drawFaceExpressions(canvas, results)
        ]).then((this.isLoading = false));
      }, 100);
    },
    takeScreenshot() {
      this.$refs.canvas
        .getContext("2d")
        .drawImage(this.$refs.video, 0, 0, 320, 180);
      let image = this.$refs.canvas.toDataURL("image/png");
      this.$refs.link.href = image;
      this.$refs.link.download = "Happy Face.png";
      this.$refs.link.innerHTML = "Download your happy face pic!!";
    },
    async checkHappiness() {
      if (this.happy > 0.98) {
        this.Score = this.happy;
        await this.takeScreenshot();
        this.isLaugh = true;
        this.smileStage = this.playingVideoID;
      }
    },
    checkPlayer() {
      const player = this.$refs.player;
      if (player.player.ended === true && this.nextStage === false) {
        this.nextStage = true;
      }
    },
    toNextStage() {
      this.nextStage = false;
      this.playingVideoID += 1;
      if (this.playingVideoID === this.videoID.length) {
        this.$refs.video.srcObject.getTracks().forEach(track => {
          track.stop();
        });
        this.nextStage = false;
        this.playingVideoID = this.videoID.length + 1;
        this.isFinished = true;
      }
    }
  }
};
</script>
<style>
canvas {
  position: fixed;
  right: 1%;
  bottom: 15%;
}
h4 {
  font-weight: 100;
  color: #fcf;
  animation: pop 2.4s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.rules {
  display: flex;
  flex-direction: row;
  background-color: white;
  box-shadow: 0 0.1875rem 1.5rem;
  border-radius: 0.375rem;
  overflow: hidden;
  position: fixed;
  right: 1%;
  top: 3%;
  color: rgb(64, 64, 64);
  width: 320px;
}
.gauge {
  display: flex;
  flex-direction: row;
  right: 1%;
  top: 15%;
  position: fixed;
  color: rgb(64, 64, 64);
  width: 320px;
}
.video-container {
  width: 750px;
  display: table-cell;
  vertical-align: middle;
  padding-left: 2%;
  top: 3%;
  position: fixed;
}
.panel {
  display: inline-block;
  text-transform: uppercase;
  font-size: 1rem;
  font-weight: 700;
  line-height: 1;
  letter-spacing: 0.0625rem;
  margin: 0.75rem;
  padding: 0.25rem;
  border-bottom: 0.125rem solid;
}
.panel-title {
  font-size: 1.125rem;
  line-height: 1.4;
  font-weight: 700;
  margin: 0.75rem;
}
.panel-footer {
  font-size: 0.875rem;
  line-height: 1;
  margin: 1.125rem 0 0 0;
  padding: 1.125rem 0 0 0;
  border-top: 0.0625rem solid;
}
.result {
  position: fixed;
  align-self: auto;
  top: auto;
  left: auto;
  margin: 5% 30%;
}
.resultW {
  position: fixed;
  align-self: auto;
  top: auto;
  left: auto;
  margin: 5% 35%;
}
@keyframes pop {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(0.7);
  }
  100% {
    transform: scale(1);
  }
}
.stream {
  position: fixed;
  right: 1%;
  bottom: 15%;
  margin: 0 auto;
  border-radius: 3%;
}
a.button {
  display: inline-block;
  padding: 0.3em 1.2em;
  margin: 0 0.1em 0.1em 0;
  border: 0.16em solid rgba(255, 255, 255, 0);
  border-radius: 2em;
  box-sizing: border-box;
  text-decoration: none;
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  color: #ffffff;
  text-shadow: 0 0.04em 0.04em rgba(0, 0, 0, 0.35);
  text-align: center;
  transition: all 0.2s;
}
a.button:hover {
  border-color: rgba(255, 255, 255, 1);
}
@media all and (max-width: 30em) {
  a.button {
    display: block;
    margin: 0.2em auto;
  }
}
.lds-circle {
  display: inline-block;
  transform: translateZ(1px);
  padding-top: 10%;
}
.lds-circle > div {
  display: inline-block;
  width: 51px;
  height: 51px;
  margin: 6px;
  border-radius: 50%;
  background: #fcf;
  animation: lds-circle 2.4s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
@keyframes lds-circle {
  0%,
  100% {
    animation-timing-function: cubic-bezier(0.5, 0, 1, 0.5);
  }
  0% {
    transform: rotateY(0deg);
  }
  50% {
    transform: rotateY(1800deg);
    animation-timing-function: cubic-bezier(0, 0.5, 0.5, 1);
  }
  100% {
    transform: rotateY(3600deg);
  }
}
.bounce-enter-active {
  animation: bounce-in 1.5s;
}
.bounce-leave-active {
  animation: bounce-in 1.5s reverse;
}
@keyframes bounce-in {
  0% {
    transform: scale(0);
  }
  50% {
    transform: scale(1.5);
  }
  100% {
    transform: scale(1);
  }
}
</style>